.Banner {
  width: 100%;
  height: 60px;
  background: #ffffff;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
  position: fixed;
  z-index: 1;
}

.Logo {
  width: 50px;
  float: left;
  margin-left: 15px;
  margin-top: 5px;
}

@font-face{
  font-family:Body;
  src:url('Fonts/Roboto-Light.ttf');
}

@font-face{
  font-family:Heading;
  src:url('Fonts/Roboto-Medium.ttf');
}

#heading{
  font-family: Heading;
}

p, h1{
  font-family: Body;
}

#mobileMenuIcon{
  display: none;
}
li{
  color: black;
}

li:hover{
  color: #4d5da8;
}


#mobileMenu{
  width: 100%;
  height: auto;
  background: black;
  text-align: center;
  color: white;
  padding-bottom: 5px;
  padding-top: 5px;
  display: none;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
}

#mainBanner{
  width: 100%;
  height: 650px;
  background: url(images/background.jpg);
  background-size: cover;

}

#blueInnerBanner{
  width: 30%;
  height: 650px;
  background: rgba(77, 93, 168, 0.6784313725490196);
  text-align: center;
  color: white;
  -webkit-box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.27058823529411763);
  float: left;
}


button{
  width: 120px;
  height: 40px;
  background: white;
  border: 0px;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}

#mainPromoBanner{
  float: right;
  height: 300px;
  width: 500px;
  margin-top: 320px;
  margin-right: 60px;
  background: url('images/updateOne.jpg');
  background-size: cover;
}

#mainBannerHeading{
  margin-top: 200px;
}

#mainBanner:hover{
  -webkit-transform: skew(5);
      -ms-transform: skew(5);
          transform: skew(5)
}

/*Responssiveness*/
@media (max-width:1000px){

  #mobileMenuIcon{
    display: block;
  }

  #desktopmenu{
    display: none;
  }

  #blueInnerBanner{
    width: 100%;
    height: 380px;
    float: none;
  }

  #mainPromoBanner{
    float: none;
    width: 100%;
    margin-top: 0px;
  }

  li{
    color: white;
  }

  #mainBannerHeading{
    margin-top: 70px;
  }



}


